import React from 'react';
import { Avatar, List } from 'antd';
import { EnvironmentFilled } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import styles from './index.less';

const selector = (store: any) => ({
  cityData: store.xzCityData,
  setCenterAndZoom: store.setCenterAndZoom,
});

function App() {
  const { cityData, setCenterAndZoom } = useStore(selector, shallow);
  const handleSetCenterAndZoom = (lng: number, lat: number, zoom: number) => {
    setCenterAndZoom(lng, lat, zoom);
  };

  return (
    <List
      itemLayout="horizontal"
      className={styles.container}
      dataSource={cityData}
      locale={{ emptyText: <div style={{ color: '#fff' }}>暂无数据</div> }}
      renderItem={(item: any, index) => (
        <List.Item
          key={item.forder}
          className={styles.item}
          actions={[
            <EnvironmentFilled className={styles.action} />
          ]}
          onClick={() => handleSetCenterAndZoom(item.lng, item.lat, 8)}
        >
          <List.Item.Meta
            avatar={
              <Avatar
                size="large"
                style={{
                  backgroundColor: '#fff',
                  color: `${index === 0 ? '#ff7a45' : '#ff4d4f'}`,
                  border: '3px solid currentColor',
                  lineHeight:'34px',
                  margin: '2px 0',
                }}
              >
                {(item.name || '').substr(0, 2)}
              </Avatar>
            }
            title={<span style={{ color: 'rgba(255,255,255,0.85)', marginBottom: '-4px', display: 'block' }}>{`${item.name}${index === 0 ? '(当前)' : ''}`}</span>}
            description={<span style={{ color: 'rgba(255,255,255,0.65)', fontSize: '12px' }}>{item.country}</span>}
          />
        </List.Item>
      )}
    />
  );
}

export default App;
