import React, { useState } from 'react';
import { Button, ConfigProvider } from 'antd';
// import { EnvironmentFilled } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import styles from './index.less';

const selector = (store: any) => ({
  carTrack: store.carTrack,
});

function App() {
  const { carTrack } = useStore(selector, shallow);
  const [isDrive, setIsDrive] = useState(false);

  const handleDriveCar = (isDriveState: boolean) => {
    if (isDriveState) {
      carTrack.start();
    } else {
      carTrack.pause();
    }
    setIsDrive(isDriveState);
  };

  return (
    <div className={styles.drive}>
      <ConfigProvider
        theme={{ token: { colorPrimary: isDrive ? '#c41d7f' : '#08979c', borderRadius: 2 } }}
      >
        <Button
          style={{ width: '100%' }}
          type="primary"
          size="large"
          disabled={!carTrack}
          onClick={() => handleDriveCar(!isDrive)}>
            {isDrive ? '休息一下' : '与他同行'}
        </Button>
      </ConfigProvider>
    </div>
  );
}

export default App;
