import { create } from 'zustand';
import { loadScript } from '@/utils/tools';
import { xzCityData, xzLineData } from '../services/data';
import { markerSvgString, svgToBase64 } from '../utils/tools';
import carImage from '@/assets/image/car.png';

export const useStore = create((set: any, get: any) => ({
  // 天地图对象
  tmap: null,
  // 控件对象
  mapControl: {},
  // 地图参数
  mapOptions: {},
  // 控件参数
  controlOptions: {},
  // 主题参数
  themeOptions: {},
  // 默认值
  defaultMapOptions: {
    // 是否启用地图拖拽
    isEnableDrag: true,
    // 是否启用滚轮放大缩小
    isScrollWheelZoom: true,
    // 是否启用双击放大
    isDoubleClickZoom: true,
    // 是否启用键盘操作
    isKeyboard: true,
    // 是否启用惯性操作
    isInertia: true,
    // 是否启用连续缩放
    isContinuousZoom: true,
    // 是否启用双指缩放
    isPinchToZoom: true,
    // 是否启用自动适应容器尺寸
    isAutoResize: true,
  },
  defaultControlOptions: {
    // 是否展示版权控件
    isShowCopyright: false,
    // 是否展示缩放控件
    isShowZoomControl: true,
    // 是否展示比例尺
    isShowScaleControl: true,
    // 是否展示鹰眼控件
    isShowOverviewMap: false,
    // 是否显示地图类型
    isShowMapTypeControl: false,
    // 手动设置地图类型
    mapType: 'TMAP_NORMAL_MAP'
  },
  defaultThemeOptions: {
    label: '月光银',
    value: 'light',
    setting: {
      container: { BColor: '#f2f2f2' },
      base: { opacity: 0.5, grayscale: 1, invert: 0, hueRotate: '0deg' },
      cover: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
    }
  },
  defaultLocation: {
    country: '中国',
    city: '北京',
    lat: '39.923615',
    level: 11,
    lon: '116.38094',
    gb: '110000',
  },
  // 玄奘西行数据源
  xzCityData: xzCityData,
  xzLineData: [],
  locationData: {},
  // 车辆对象
  carTrack: null,
  // 初始化地图
  initMap(map: any, locationData: any = {}) {
    let tmap = get().tmap;
    const defaultLocation = get().defaultLocation;
    if (tmap) {
      console.log('天地图已初始化过，不会重复触发');
      return;
    }
    if (!map) {
      console.log('天地图初始化失败，参数不存在');
      return;
    }
    tmap = map;
    console.log('天地图初始化完成');
    set({ tmap, locationData: locationData || defaultLocation });
    tmap.centerAndZoom(new (window as any).T.LngLat(locationData.lng || defaultLocation.lng, locationData.lat || defaultLocation.lat), 5);
  },
  // 修改主题配置
  updateThemeOptions(theme: any = {}) {
    set({ themeOptions: JSON.parse(JSON.stringify(theme)) });
    // 修改样式
    const containerDom = document.getElementById('tiandituMap');
    if (containerDom) {
      containerDom.style.setProperty('--tmap-container-bcolor', theme?.setting?.container?.BColor || '#fff');
      containerDom.style.setProperty('--tmap-base-opacity', theme?.setting?.base?.opacity || 1);
      containerDom.style.setProperty('--tmap-base-grayscale', theme?.setting?.base?.grayscale || 0);
      containerDom.style.setProperty('--tmap-base-invert', theme?.setting?.base?.invert || 0);
      containerDom.style.setProperty('--tmap-base-hue-rotate', theme?.setting?.base?.hueRotate || '0deg');
      containerDom.style.setProperty('--tmap-cover-opacity', theme?.setting?.cover?.opacity || 1);
      containerDom.style.setProperty('--tmap-cover-grayscale', theme?.setting?.cover?.grayscale || 0);
      containerDom.style.setProperty('--tmap-cover-invert', theme?.setting?.cover?.invert || 0);
      containerDom.style.setProperty('--tmap-cover-hue-rotate', theme?.setting?.cover?.hueRotate || '0deg');
    }
  },
  // 修改参数配置
  updateMapOptions(options: any = {}) {
    const mapOptions = get().mapOptions;
    const tmap = get().tmap;
    Object.keys(options).map(key => {
      if (mapOptions[key] !== options[key]) {
        mapOptions[key] = options[key];
        // 是否启用地图拖拽
        if (key === 'isEnableDrag') {
          if (mapOptions.isEnableDrag) { tmap.enableDrag(); } else { tmap.disableDrag(); }
        }
        // 是否启用滚轮放大缩小
        if (key === 'isScrollWheelZoom') {
          if (mapOptions.isScrollWheelZoom) { tmap.enableScrollWheelZoom(); } else { tmap.disableScrollWheelZoom(); }
        }
        // 是否启用双击放大
        if (key === 'isDoubleClickZoom') {
          if (mapOptions.isDoubleClickZoom) { tmap.enableDoubleClickZoom(); } else { tmap.disableDoubleClickZoom(); }
        }
        // 是否启用键盘操作
        if (key === 'isKeyboard') {
          if (mapOptions.isKeyboard) { tmap.enableKeyboard(); } else { tmap.disableKeyboard(); }
        }
        // 是否启用惯性操作
        if (key === 'isInertia') {
          if (mapOptions.isInertia) { tmap.enableInertia(); } else { tmap.disableInertia(); }
        }
        // 是否启用连续缩放
        if (key === 'isContinuousZoom') {
          if (mapOptions.isContinuousZoom) { tmap.enableContinuousZoom(); } else { tmap.disableContinuousZoom(); }
        }
        // 是否启用双指缩放
        if (key === 'isPinchToZoom') {
          if (mapOptions.isPinchToZoom) { tmap.enablePinchToZoom(); } else { tmap.disablePinchToZoom(); }
        }
        // 是否启用自动适应容器尺寸
        if (key === 'isAutoResize') {
          if (mapOptions.isAutoResize) { tmap.enableAutoResize(); } else { tmap.disableAutoResize(); }
        }
      }
    });
    set({ mapOptions });
  },
  // 修改控件对象
  updateControlOptions(options: any = {}) {
    const controlOptions = get().controlOptions;
    const mapControl = get().mapControl;
    const tmap = get().tmap;

    // 添加Control控件
    const addControl = (id: string, control: any) => {
      if (!mapControl[id]) {
        mapControl[id] = control;
        tmap.addControl(control);
      }
    };
    // 删除Control控件
    const removeControl = (id: string) => {
      if (mapControl[id]) {
        tmap.removeControl(mapControl[id]);
        delete mapControl[id];
      }
    };

    Object.keys(options).map(key => {
      if (controlOptions[key] !== options[key]) {
        controlOptions[key] = options[key];
        // 是否展示版权控件
        // isShowCopyright: true,
        if (key === 'isShowCopyright') {
          const copyrightDoms = document.getElementsByClassName('tdt-control-copyright');
          if (controlOptions.isShowCopyright) {
            for (let i = 0; i < copyrightDoms.length; i++) {
              (copyrightDoms[i] as any).style.display = 'block';
            }
          } else {
            for (let i = 0; i < copyrightDoms.length; i++) {
              (copyrightDoms[i] as any).style.display = 'none';
            }
          }
        }
        // 是否展示缩放控件
        if (key === 'isShowZoomControl') {
          const control = new (window as any).T.Control.Zoom({ position: (window as any).T_ANCHOR_TOP_RIGHT });
          if (controlOptions.isShowZoomControl) { addControl('zoomControl', control); } else { removeControl('zoomControl'); }
        }
        // 是否展示鹰眼控件
        if (key === 'isShowOverviewMap') {
          const control = new (window as any).T.Control.OverviewMap({
            position: (window as any).T_ANCHOR_BOTTOM_RIGHT,
            isOpen: true,
          });
          if (controlOptions.isShowOverviewMap) { addControl('overviewMap', control); } else { removeControl('overviewMap'); }
        }
        // 是否展示比例尺
        if (key === 'isShowScaleControl') {
          const control = new (window as any).T.Control.Scale({ position: (window as any).T_ANCHOR_BOTTOM_RIGHT });
          if (controlOptions.isShowScaleControl) { addControl('scaleControl', control); } else { removeControl('scaleControl'); }
        }
        // 是否显示地图类型
        if (key === 'isShowMapTypeControl') {
          const control = new (window as any).T.Control.MapType();
          if (controlOptions.isShowMapTypeControl) { addControl('mapTypeControl', control); } else { removeControl('mapTypeControl'); }
        }
      }
    });
    set({ controlOptions });
  },
  // 手动设置地图类型
  setMapType(type: string = 'TMAP_NORMAL_MAP') {
    const tmap = get().tmap;
    const controlOptions = get().controlOptions;
    if (['TMAP_NORMAL_MAP', 'TMAP_SATELLITE_MAP', 'TMAP_HYBRID_MAP', 'TMAP_TERRAIN_MAP', 'TMAP_TERRAIN_HYBRID_MAP'].includes(type)) {
      if (controlOptions.mapType !== type) {
        tmap.removeControl((window as any)[type]);
        tmap.addControl((window as any)[type]);
        controlOptions.mapType = type;
        set({ controlOptions });
      }
    }
  },
  initCityOverLay() {
    const tmap = get().tmap;
    const locationData = get().locationData;
    // 创建图片对象
    const icon: any = new (window as any).T.Icon({
      iconUrl: svgToBase64(markerSvgString((locationData.city || '').slice(0,2), '#ff7a45')),
      iconSize: new (window as any).T.Point(32, 40),
      iconAnchor: new (window as any).T.Point(16, 40)
    });
    // 标注当前
    const markerItem = new (window as any).T.Marker(new (window as any).T.LngLat(locationData.lng, locationData.lat), { icon: icon });
    // 向地图上添加标注
    tmap.addOverLay(markerItem);
    // 标注玄奘经过的城市
    xzCityData.forEach((item: any) => {
      //创建图片对象
      const icon: any = new (window as any).T.Icon({
        iconUrl: svgToBase64(markerSvgString((item.name || '').slice(0,2), '#ff4d4f')),
        iconSize: new (window as any).T.Point(32, 40),
        iconAnchor: new (window as any).T.Point(16, 40)
      });
      // 创建标注对象
      const markerItem = new (window as any).T.Marker(new (window as any).T.LngLat(item.lng, item.lat), { icon: icon });
      // 向地图上添加标注
      tmap.addOverLay(markerItem);
      // 信息框
      markerItem.addEventListener('click', (e: any) => {
        const infoWin = new (window as any).T.InfoWindow(
          `${item.name}：${item.description}`,
          { offset: new (window as any).T.Point(0, -30) }
        ); // 创建信息窗口对象
        tmap.openInfoWindow(infoWin, e.lnglat); //开启信息窗口
      });
    });
    set({
      xzCityData: [{
        country: locationData.countryShort,
        name: locationData.city,
        description: '',
        forder: 0,
        lng: locationData.lng,
        lat: locationData.lat,
      },
      ...xzCityData],
      locationData,
    });
  },
  initLineOverLay() {
    const tmap = get().tmap;
    const xzCityData = get().xzCityData;
    // 标注当前
    const line = new (window as any).T.Polyline([
      new (window as any).T.LngLat(xzCityData[0].lng, xzCityData[0].lat),
      new (window as any).T.LngLat(xzCityData[1].lng, xzCityData[1].lat),
    ], { color: '#ffc53d', lineStyle: 'dashed' });
    //向地图上添加线
    tmap.addOverLay(line);
    // 标注玄奘
    xzLineData.forEach((item: any) => {
      const points = item.geometry.coordinates.map((coordinate: any) => {
        return new (window as any).T.LngLat(coordinate[0], coordinate[1]);
      });
      //创建线对象
      const line = new (window as any).T.Polyline(points, { color: '#ff4d4f', lineStyle: 'dashed' });
      //向地图上添加线
      tmap.addOverLay(line);
    });
    set({ xzLineData });
  },
  // 设置中心点
  setCenterAndZoom(lng: number, lat: number, zoom: number) {
    const tmap = get().tmap;
    tmap.centerAndZoom(new (window as any).T.LngLat(lng, lat), zoom);
  },
  // 初始化设置车辆
  initCarTrack() {
    Promise.all([
      loadScript('//cdn.bootcss.com/d3/3.5.17/d3.js'),
      // loadScript('//lbs.tianditu.gov.cn/api/js4.0/opensource/openlibrary/D3SvgOverlay.js'),
      // loadScript('//lbs.tianditu.gov.cn/api/js4.0/opensource/openlibrary/CarTrack.js'),
      import('@/assets/lib/D3SvgOverlay.js' as any),
      import('@/assets/lib/CarTrack.js' as any),
    ]).then(() => {
      const tmap = get().tmap;
      const xzCityData = get().xzCityData;
      const points = xzCityData.map((coordinate: any) => {
        return new (window as any).T.LngLat(coordinate.lng, coordinate.lat);
      });
      const carTrack = new (window as any).T.CarTrack(tmap, {
        interval: 5,
        speed: 500,
        dynamicLine: true,
        polylinestyle: { color: "#ff7a45", weight: 5, opacity: 1 },
        Datas: points,
        passOneNode: (lnglat: any, index: any, length: any) => {
          if (index % 500 === 0 || index + 1 === length) {
            tmap.centerAndZoom(new (window as any).T.LngLat(lnglat.lng, lnglat.lat), tmap.getZoom());
          }
        },
        carstyle: {
          display: true,
          iconUrl: carImage,
          width: 52,
          height: 26
        },
      });
      set({ carTrack: carTrack });
      carTrack.start();
      setTimeout(() => {
        carTrack.pause();
      }, 10);
    });
  },
}));
