import React, { useEffect, useState } from 'react';
import { shallow } from 'zustand/shallow';
import { Spin } from 'antd';
import { useStore } from '@/module/store';
import { loadScript } from '@/utils/tools';
import { getTiandituApiHost } from '@/module/config';
import { getLocationIp } from '@/module/services/api';
import styles from './index.less';

const selector = (store: any) => ({
  initMap: store.initMap,
  initMapOptions: () => store.updateMapOptions(store.defaultMapOptions),
  initControlOptions: () => store.updateControlOptions(store.defaultControlOptions),
  initThemeOptions: () => store.updateThemeOptions(store.defaultThemeOptions),
  initCityOverLay: store.initCityOverLay,
  initLineOverLay: store.initLineOverLay,
  initLocationOverLay: store.initLocationOverLay,
  initCarTrack: store.initCarTrack,
});

function Map() {
  const store = useStore(selector, shallow);
  const [initLoading, setInitLoading] = useState(true);
  useEffect(() => {
    Promise.all([
      getLocationIp(),
      loadScript(getTiandituApiHost()),
    ]).then(result => {
      setInitLoading(false);
      const [ resultLocationIp, resultLoadScript ] = result;
      let locationData = {};
      if (resultLocationIp.code === 200) {
        locationData = resultLocationIp.data;
      }
      // 初始化地图
      const map = new (window as any).T.Map('tiandituMap');
      store.initMap(map, locationData);
      store.initMapOptions();
      store.initControlOptions();
      store.initThemeOptions();
      store.initCityOverLay();
      store.initLineOverLay();
      store.initCarTrack();
    });
  }, []);
  return (
    <Spin spinning={initLoading}>
      <div id='tiandituMap' className={styles.map} />
    </Spin>
  );
}

export default Map;
